body {
  background: #82c0f4;
  font-family: Arial, Helvetica, sans-serif;
}

.buttons-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.bounds-form-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  margin-top: 16px;
  display: none;
  align-items: center;
  justify-content: center;
}

.radius-form-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  margin-top: 16px;
  display: none;
  align-items: center;
  justify-content: center;
}

.form-actions-wrapper {
  display: flex;
  gap: 8px;
}

.form-ui-wrapper {
  width: 340px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.input-row {
  display: flex;
  gap: 6px;
}

.radius-button {
  margin-left: 12px;
}

.location-status-wrapper {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.location-status {
  display: none;
  background: white;
  border-radius: 6px;
  padding: 16px 20px 16px 20px;
  margin-bottom: 16px;
}

button,
input {
  background: white;
  border-radius: 6px;
  border: none;
  padding: 12px 12px 12px 12px;
}

input {
  width: 100%;
}
